import React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Paper,
  Slider,
  Button,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  FormHelperText,
} from '@mui/material';
import { useSettings, Settings } from '../context/SettingsContext';

const modeDescriptions = {
  normal: ' :       .       .',
  sprint: ' :       .   \'     .',
  marathon: ' :        .       .'
};

const SettingsPage = () => {
  const navigate = useNavigate();
  const { settings, updateSettings } = useSettings();

  const handleStart = () => {
    navigate('/trainer');
  };

  const handleSettingsChange = (key: keyof typeof settings) => (
    event: React.ChangeEvent<HTMLInputElement> | any
  ) => {
    const value = event.target.value;
    updateSettings({ [key]: value });
  };

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 4, mb: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom align="center">
          
        </Typography>
        <Paper elevation={3} sx={{ p: 3, mt: 3 }}>
          <Box sx={{ mb: 3 }}>
            <Typography gutterBottom>
               (  ): {settings.wordsPerMinute}
            </Typography>
            <Slider
              value={settings.wordsPerMinute}
              onChange={handleSettingsChange('wordsPerMinute')}
              min={100}
              max={1000}
              step={50}
            />
          </Box>

          <Box sx={{ mb: 3 }}>
            <Typography gutterBottom>
               : {settings.fontSize}px
            </Typography>
            <Slider
              value={settings.fontSize}
              onChange={handleSettingsChange('fontSize')}
              min={16}
              max={48}
              step={2}
            />
          </Box>

          <FormControl fullWidth sx={{ mb: 2 }}>
            <InputLabel>   </InputLabel>
            <Select
              value={settings.wordsAtOnce}
              onChange={(e) => updateSettings({ wordsAtOnce: e.target.value as Settings['wordsAtOnce'] })}
              label="   "
            >
              <MenuItem value={1}>1 </MenuItem>
              <MenuItem value={2}>2 </MenuItem>
              <MenuItem value={3}>3 </MenuItem>
              <MenuItem value={4}>4 </MenuItem>
              <MenuItem value={5}>5 </MenuItem>
              <MenuItem value={6}>6 </MenuItem>
              <MenuItem value={7}>7 </MenuItem>
            </Select>
            <FormHelperText>
                ,     
            </FormHelperText>
          </FormControl>

          <FormControl fullWidth sx={{ mb: 3 }}>
            <InputLabel> </InputLabel>
            <Select
              value={settings.mode}
              onChange={handleSettingsChange('mode')}
              label=" "
            >
              <MenuItem value="normal"> </MenuItem>
              <MenuItem value="sprint"> </MenuItem>
              <MenuItem value="marathon"> </MenuItem>
            </Select>
            <FormHelperText sx={{ 
              mt: 1, 
              backgroundColor: 'rgba(0, 0, 0, 0.03)', 
              p: 1, 
              borderRadius: 1,
              minHeight: '60px'
            }}>
              {modeDescriptions[settings.mode as keyof typeof modeDescriptions]}
            </FormHelperText>
          </FormControl>

          <Button
            variant="contained"
            color="primary"
            fullWidth
            size="large"
            onClick={handleStart}
          >
             
          </Button>
        </Paper>
      </Box>
    </Container>
  );
};

export default SettingsPage;
