import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Paper,
  Button,
  List,
  ListItem,
  ListItemText,
} from '@mui/material';
import EmojiEventsIcon from '@mui/icons-material/EmojiEvents';

const ResultsPage = () => {
  const navigate = useNavigate();
  const [results, setResults] = useState({
    duration: 0,
    totalWords: 0,
    wordsPerMinute: 0
  });

  useEffect(() => {
    const readingTime = localStorage.getItem('readingTime');
    const totalWords = localStorage.getItem('totalWords');
    
    if (!readingTime || !totalWords) {
      navigate('/');
      return;
    }

    const duration = parseFloat(readingTime);
    const words = parseInt(totalWords, 10);
    const wpm = Math.round((words / duration) * 60);

    setResults({
      duration,
      totalWords: words,
      wordsPerMinute: wpm
    });
  }, [navigate]);

  const formatDuration = (seconds: number) => {
    if (seconds < 60) {
      return `${Math.round(seconds)} ${getUkrainianSuffix(Math.round(seconds))}`;
    }
    const minutes = Math.floor(seconds / 60);
    const remainingSeconds = Math.round(seconds % 60);
    return `${minutes} ${getUkrainianSuffix(minutes)} ${remainingSeconds} ${getUkrainianSuffix(remainingSeconds)}`;
  };

  const getUkrainianSuffix = (number: number) => {
    const lastDigit = number % 10;
    const lastTwoDigits = number % 100;

    if (lastTwoDigits >= 11 && lastTwoDigits <= 14) {
      return '';
    }

    if (lastDigit === 1) {
      return '';
    }
    if (lastDigit >= 2 && lastDigit <= 4) {
      return '';
    }
    return '';
  };

  const formatWords = (count: number) => {
    const lastDigit = count % 10;
    const lastTwoDigits = count % 100;

    if (lastTwoDigits >= 11 && lastTwoDigits <= 14) {
      return '';
    }

    if (lastDigit === 1) {
      return '';
    }
    if (lastDigit >= 2 && lastDigit <= 4) {
      return '';
    }
    return '';
  };

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 4, textAlign: 'center' }}>
        <EmojiEventsIcon sx={{ fontSize: 60, color: 'primary.main', mb: 2 }} />
        <Typography variant="h4" component="h1" gutterBottom>
           
        </Typography>

        <Paper elevation={3} sx={{ p: 3, mt: 3 }}>
          <List>
            <ListItem>
              <ListItemText
                primary={
                  <Typography variant="h6">
                     : {formatDuration(results.duration)}
                  </Typography>
                }
              />
            </ListItem>
            <ListItem>
              <ListItemText
                primary={
                  <Typography variant="h6">
                     : {results.totalWords} {formatWords(results.totalWords)}
                  </Typography>
                }
              />
            </ListItem>
            <ListItem>
              <ListItemText
                primary={
                  <Typography variant="h6">
                    : {results.wordsPerMinute}   
                  </Typography>
                }
              />
            </ListItem>
          </List>
        </Paper>

        <Box sx={{ mt: 3 }}>
          <Button
            variant="contained"
            color="primary"
            onClick={() => navigate('/')}
            size="large"
            fullWidth
          >
             
          </Button>
        </Box>
      </Box>
    </Container>
  );
};

export default ResultsPage;
