import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  TextField,
  Button,
  Paper,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  Slider,
  FormControlLabel,
  Switch,
  FormHelperText,
} from '@mui/material';
import { useSettings, Settings } from '../context/SettingsContext';

const HomePage = () => {
  const navigate = useNavigate();
  const { settings, updateSettings } = useSettings();
  const [text, setText] = useState('');

  const handleTextChange = (event: React.ChangeEvent<HTMLTextAreaElement>) => {
    setText(event.target.value);
  };

  const handleStart = () => {
    if (text.trim()) {
      localStorage.setItem('trainingText', text);
      navigate('/trainer');
    }
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 4, mb: 4 }}>
        <Typography variant="h4" component="h1" gutterBottom align="center">
            
        </Typography>

        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
              
          </Typography>
          <TextField
            fullWidth
            multiline
            rows={10}
            value={text}
            onChange={handleTextChange}
            placeholder="   ..."
            variant="outlined"
            sx={{ mb: 2 }}
          />
        </Paper>

        <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
          <Typography variant="h6" gutterBottom>
            
          </Typography>

          <Box sx={{ mb: 3 }}>
            <Typography gutterBottom>
               (  ): {settings.wordsPerMinute}
            </Typography>
            <Slider
              value={settings.wordsPerMinute}
              onChange={(_, value) => updateSettings({ wordsPerMinute: value as number })}
              min={100}
              max={1000}
              step={50}
            />
          </Box>

          <Box sx={{ mb: 3 }}>
            <Typography gutterBottom>
               : {settings.fontSize}px
            </Typography>
            <Slider
              value={settings.fontSize}
              onChange={(_, value) => updateSettings({ fontSize: value as number })}
              min={16}
              max={48}
              step={2}
            />
          </Box>

          <FormControl fullWidth sx={{ mb: 3 }}>
            <InputLabel>   </InputLabel>
            <Select
              value={settings.wordsAtOnce}
              onChange={(e) => updateSettings({ wordsAtOnce: e.target.value as Settings['wordsAtOnce'] })}
              label="   "
            >
              <MenuItem value={1}>1 </MenuItem>
              <MenuItem value={2}>2 </MenuItem>
              <MenuItem value={3}>3 </MenuItem>
              <MenuItem value={4}>4 </MenuItem>
              <MenuItem value={5}>5 </MenuItem>
              <MenuItem value={6}>6 </MenuItem>
              <MenuItem value={7}>7 </MenuItem>
            </Select>
            <FormHelperText>
                ,     
            </FormHelperText>
          </FormControl>

          <FormControl fullWidth sx={{ mb: 3 }}>
            <InputLabel> </InputLabel>
            <Select
              value={settings.mode}
              onChange={(e) => updateSettings({ mode: e.target.value as 'normal' | 'sprint' | 'marathon' })}
              label=" "
            >
              <MenuItem value="normal"> </MenuItem>
              <MenuItem value="sprint"> </MenuItem>
              <MenuItem value="marathon"> </MenuItem>
            </Select>
          </FormControl>

          <FormControlLabel
            control={
              <Switch
                checked={settings.showFixationPoint}
                onChange={(e) => updateSettings({ showFixationPoint: e.target.checked })}
              />
            }
            label="  "
          />
        </Paper>

        <Box sx={{ display: 'flex', justifyContent: 'center' }}>
          <Button
            variant="contained"
            color="primary"
            size="large"
            onClick={handleStart}
            disabled={!text.trim()}
          >
             
          </Button>
        </Box>
      </Box>
    </Container>
  );
};

export default HomePage;
