import React, { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Container,
  Typography,
  Box,
  Button,
  Paper,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  Slider,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Switch,
  FormControlLabel,
  SelectChangeEvent,
} from '@mui/material';
import SettingsIcon from '@mui/icons-material/Settings';
import { useSettings, Settings } from '../context/SettingsContext';

const TrainerPage = () => {
  const navigate = useNavigate();
  const { settings, updateSettings } = useSettings();
  const [text] = useState(localStorage.getItem('trainingText') || '');
  const [words, setWords] = useState<string[]>([]);
  const [currentWordIndex, setCurrentWordIndex] = useState(0);
  const [isRunning, setIsRunning] = useState(false);
  const [startTime, setStartTime] = useState<number | null>(null);
  const [showSettings, setShowSettings] = useState(false);
  const [pauseStartTime, setPauseStartTime] = useState<number | null>(null);
  const [totalPauseDuration, setTotalPauseDuration] = useState(0);

  useEffect(() => {
    if (!text) {
      navigate('/');
      return;
    }
    setWords(text.split(/\s+/).filter(word => word.trim() !== ''));
  }, [text, navigate]);

  const getCurrentWords = useCallback(() => {
    if (currentWordIndex >= words.length) return '';
    return words
      .slice(currentWordIndex, currentWordIndex + settings.wordsAtOnce)
      .join(' ');
  }, [currentWordIndex, words, settings.wordsAtOnce]);

  useEffect(() => {
    let interval: NodeJS.Timeout;
    if (isRunning && currentWordIndex < words.length) {
      if (!startTime) setStartTime(Date.now());
      interval = setInterval(() => {
        setCurrentWordIndex((prev) => {
          const next = prev + settings.wordsAtOnce;
          if (next >= words.length) {
            setIsRunning(false);
            const endTime = Date.now();
            const totalDuration = (endTime - (startTime || endTime)) / 1000;
            const actualReadingTime = totalDuration - (totalPauseDuration / 1000);
            
            localStorage.setItem('readingTime', actualReadingTime.toString());
            localStorage.setItem('totalWords', words.length.toString());
            navigate('/results');
          }
          return next;
        });
      }, (60 / settings.wordsPerMinute) * 1000);
    }
    return () => clearInterval(interval);
  }, [isRunning, currentWordIndex, words.length, settings.wordsPerMinute, settings.wordsAtOnce, navigate, startTime, totalPauseDuration]);

  const toggleReading = () => {
    if (isRunning) {
      //   
      setPauseStartTime(Date.now());
    } else {
      //  
      if (pauseStartTime) {
        const pauseDuration = Date.now() - pauseStartTime;
        setTotalPauseDuration(prev => prev + pauseDuration);
        setPauseStartTime(null);
      }
    }
    setIsRunning(!isRunning);
  };

  const resetReading = () => {
    setCurrentWordIndex(0);
    setIsRunning(false);
    setStartTime(null);
    setPauseStartTime(null);
    setTotalPauseDuration(0);
  };

  const handleWordsAtOnceChange = (e: SelectChangeEvent<number>) => {
    const value = e.target.value as Settings['wordsAtOnce'];
    updateSettings({ wordsAtOnce: value });
  };

  return (
    <Container maxWidth="md">
      <Box sx={{ mt: 4, textAlign: 'center' }}>
        <Paper 
          elevation={3} 
          sx={{ 
            p: 4,
            minHeight: '200px',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
            position: 'relative'
          }}
        >
          {settings.showFixationPoint && (
            <Box
              sx={{
                position: 'absolute',
                width: '10px',
                height: '10px',
                backgroundColor: 'red',
                borderRadius: '50%',
                left: '50%',
                transform: 'translateX(-50%)',
                top: '45%'
              }}
            />
          )}
          <Typography 
            variant="h4" 
            gutterBottom
            sx={{ 
              fontSize: settings.fontSize,
              mb: 4
            }}
          >
            {getCurrentWords() || '?'}
          </Typography>
        </Paper>

        <Box sx={{ mt: 3, display: 'flex', justifyContent: 'center', gap: 2 }}>
          <Button
            variant="contained"
            color={isRunning ? "error" : "primary"}
            onClick={toggleReading}
            size="large"
          >
            {isRunning ? '' : ''}
          </Button>
          <Button
            variant="outlined"
            onClick={resetReading}
            size="large"
          >
            
          </Button>
          <IconButton 
            color="primary"
            onClick={() => setShowSettings(true)}
            disabled={isRunning}
            size="large"
          >
            <SettingsIcon />
          </IconButton>
        </Box>
      </Box>

      <Dialog 
        open={showSettings} 
        onClose={() => setShowSettings(false)}
        maxWidth="sm"
        fullWidth
      >
        <DialogTitle></DialogTitle>
        <DialogContent>
          <Box sx={{ pt: 2 }}>
            <Box sx={{ mb: 3 }}>
              <Typography gutterBottom>
                  (  ): {settings.wordsPerMinute}
              </Typography>
              <Slider
                value={settings.wordsPerMinute}
                onChange={(_, value) => updateSettings({ wordsPerMinute: value as number })}
                min={100}
                max={1000}
                step={10}
              />
            </Box>

            <Box sx={{ mb: 3 }}>
              <Typography gutterBottom>
                 : {settings.fontSize}
              </Typography>
              <Slider
                value={settings.fontSize}
                onChange={(_, value) => updateSettings({ fontSize: value as number })}
                min={12}
                max={48}
                step={2}
              />
            </Box>

            <Box sx={{ mb: 3 }}>
              <FormControl fullWidth>
                <InputLabel>   </InputLabel>
                <Select<Settings['wordsAtOnce']>
                  value={settings.wordsAtOnce}
                  onChange={handleWordsAtOnceChange}
                  label="   "
                >
                  {[1, 2, 3, 4, 5, 6, 7].map((num) => (
                    <MenuItem key={num} value={num}>
                      {num}
                    </MenuItem>
                  ))}
                </Select>
              </FormControl>
            </Box>

            <Box sx={{ mb: 3 }}>
              <FormControlLabel
                control={
                  <Switch
                    checked={settings.showFixationPoint}
                    onChange={(e) => updateSettings({ showFixationPoint: e.target.checked })}
                  />
                }
                label="  "
              />
            </Box>
          </Box>
        </DialogContent>
      </Dialog>
    </Container>
  );
};

export default TrainerPage;
